









import { Component, Prop, Vue } from 'vue-property-decorator'
import {VueConstructor} from "vue";
import IconBox from "@/components/IconBox.vue";
import IconTools from "@/components/IconTools.vue";
import IconContact from "@/components/IconContact.vue";

@Component
export default class SectionTitle extends Vue {
    @Prop() title!: String;
    @Prop() iconName!: String;

    get icon(): VueConstructor<Vue> | undefined{
        if (this.iconName === 'box') return IconBox;
        if (this.iconName === 'tools') return IconTools;
        if (this.iconName === 'contact') return IconContact;
    }

}
