









import { Component, Prop, Vue } from 'vue-property-decorator'
import {VueConstructor} from "vue";
import IconPhone from "@/components/IconPhone.vue";
import IconMail from "@/components/IconMail.vue";
import IconLocation from "@/components/IconLocation.vue";

@Component
export default class ContactRow extends Vue {
    @Prop() title!: String;
    @Prop() iconName!: String;

    get icon(): VueConstructor<Vue> | undefined{
        if (this.iconName === 'phone') return IconPhone;
        if (this.iconName === 'mail') return IconMail;
        if (this.iconName === 'location') return IconLocation;
    }

}
